//firebaseApp.firestore().settings({ timestampsInSnapshots: true })
export const addRoomToFirebase = task => {
  const id = task.id;
  database.ref(`/rooms/${id}`).set(task);
};

export const removeRoomFromFirebase = id => {
  database.ref(`/rooms/${id}`).remove();
};
